* {
    padding: 0;
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    box-sizing: border-box;
    color: #000;
    // TODO: Что-то придумать с этим
    // user-select: none;
}

body {
    overflow-x: hidden;
    position: relative;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

input, button, textarea {
    outline: none;
    background: none;
    border: none;
}

*::-webkit-scrollbar {
    width: 6px;
}
  
*::-webkit-scrollbar-track {
    background: #f0f0f0;
}
  
*::-webkit-scrollbar-thumb {
    background: #606060;
    border-radius: 10px;
}
  
*::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.container {
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
    max-width: 1300px;
}

b {
    color: #ff7d53;
    // TODO: подумать над тем, как лучше выглядит
    // text-decoration: underline;

    a {
        color: #ff7d53;
    }
}

h1 {
    font-size: 32px;
}

p {
    font-size: 16px;
}