.contacts-list {
    margin-top: 120px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    grid-auto-rows: auto;
    gap: 10px;

    li {
        padding: 20px;
        border-radius: 7px;
        display: flex;
        justify-content: space-between;

        &:nth-child(1n) {
            background-color: #e7f8ee;
        }

        &:nth-child(2n) {
            background-color: #e3e5fe;
        }

        &:nth-child(3n) {
            background-color: #f1f9fc;
            grid-column: 1 / -1;
        }

        .contacts-item-text {
            h2, p {
                margin-bottom: 20px;
            }
        }

        .contact-links {
            display: flex;
            flex-wrap: wrap;

            span, a {
                margin-left: 5px;
                margin-right: 5px;
                padding: 10px;
                border-radius: 7px;
                border: 1px solid #bababa;
                margin-top: 5px;
            }
        }

        .contacts-item-img img {
            width: 200px;
            border-radius: 50%;
            margin-left: 20px;
        }
    }
}

@media(max-width:600px) {
    .contacts-list {
        margin-top: 120px;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        grid-auto-rows: auto;
        gap: 10px;
    
        li {
            padding: 20px;
            border-radius: 7px;
            display: flex;
            justify-content: space-between;
    
            &:nth-child(1n) {
                background-color: #e7f8ee;
            }
    
            &:nth-child(2n) {
                background-color: #e3e5fe;
            }
    
            &:nth-child(3n) {
                background-color: #f1f9fc;
                grid-column: 1 / -1;
            }
    
            .contacts-item-text {
                h2, p {
                    margin-bottom: 20px;
                }
            }
    
            .contact-links {
                display: flex;
                flex-wrap: wrap;
    
                span, a {
                    margin-left: 5px;
                    margin-right: 5px;
                    padding: 10px;
                    border-radius: 7px;
                    border: 1px solid #bababa;
                    margin-top: 5px;
                }
            }
    
            .contacts-item-img img {
                width: 100px;
                border-radius: 50%;
                margin-left: 20px;
            }
        }
    }
}