.rates {
    margin-top: 160px;
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    grid-auto-rows: auto;

    .rates-item {
        display: flex;
        align-items: center;
        border: 1px solid #bababa;
        border-radius: 15px;
        padding: 20px;
        position: relative;

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            right: 0;
            border-radius: 50% 0 15px 0;
            background-color: #e3e5fe;
            width: 50px;
            height: 50px;
            display: block;
        }

        img {
            margin-right: 20px;
            width: 160px;
        }
    }
}

@media(max-width:600px) {
    .rates {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));

        .rates-item {
            flex-direction: column;
            text-align: center;
    
            &::after {
                display: none;
            }
    
            img {
                margin-right: 0;
                width: 160px;
                margin-bottom: 20px;
            }
        }
    }
}