.wiki {
    margin-top: 120px;
    padding: 20px;
    border: 1px solid #bababa;
    border-radius: 15px;

    h1, p:not(.last) {
        margin-bottom: 20px;
    }

    .flex {
        display: flex;
        align-items: center;
    }

    .wiki-numbered-list {
        li {
            list-style:disc;
            margin-left: 20px;

            a:hover {
                text-decoration: underline;
            }
        }
    }
}