.dl {
    margin-top: 160px;

    .level {
        display: flex;
        align-items: center;
        padding: 20px;
        border-radius: 15px;
        position: relative;
        border: 1px solid #bababa;
        cursor: pointer;
        margin-bottom: 30px;
        transition: .3s transform;

        &:hover {
            transform: scale(102%);
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 15px 0 15px 0;
            background-color: #e3e5fe;
            width: 50px;
            height: 50px;
            display: block;
            z-index: -1;
        }

        .points {
            position: absolute;
            bottom: 20px;
            right: 20px;
        }

        img {
            height: 90px;
            width: 150px;
            border-radius: 7px;
            margin-right: 20px;
        }
    }
}