.leaders {
    margin-top: 160px;
    border: 1px solid #bababa;
    border-radius: 15px;

    .top-leaders {
        display: flex;
        position: relative;
        padding: 20px;
        justify-content: space-between;

        .leaders-dropdown {
            position: absolute;
            right: 20px;

            button {
                padding: 10px;
                background-color: #f35613;
                color: #fff;
                border-radius: 7px;
                display: none;
                cursor: pointer;
                width: 130px;
                font-size: 17px;
                margin-bottom: 10px;
                transition: .3s background-color;

                &.active {
                    display: block
                }

                &:hover {
                    background-color: #ff7d53;
                }
            }
        }

        span {
            padding: 10px;
            background-color: #fef5e6;
            width: 100%;
            text-align: center;
            border-radius: 7px;
            margin-right: 160px;
            font-size: 17px;
        }
    }

    .leaders-list {
        margin-top: 20px;

        li {
            padding: 20px;
            cursor: pointer;
            display: flex;
            justify-content: space-between;
            transition: .3s background-color;

            &:hover {
                background-color: #f0f0f0;
            }

            &:last-child {
                border-radius: 0 0 15px 15px;
            }
        }
    }
}