.records-window {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #000000;
    opacity: 90%;
    z-index: 5;

    .records-window-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 20px;
        background-color: #fff;
        z-index: 5;
        border: 1px solid #ddd;
        border-radius: 15px;
        min-width: 400px;
    }

    h1, p {
        text-align: center;
    }

    p {
        margin-bottom: 20px;
    }

    button {
        text-align: right;
        width: 100%;
        cursor: pointer;
        margin-bottom: 20px;
        color: #6d6d6d;
        transition: .3s color;

        &:hover {
            text-decoration: underline;
            color: #000;
        }
    }

    a {
        color: #6d6d6d;
        transition: .3s color;

        &:hover {
            text-decoration: underline;
            color: #000;
        }
    }
}