footer {
    margin-top: 100px;
    border-top: 1px solid #9e9e9e;
}

.footer-body {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 40px;
}

.footer-columns {
    display: flex;
}

.footer-column {
    margin-right: 30px;
    
    a {
        text-decoration: none;
        color: #484848;
        transition: .3s color;

        &:hover {
            color: #000;
            text-decoration: underline;
        }
    }
}

@media(max-width:600px) {
    .footer-body {
        flex-direction: column;
    }
}