.fullscreen {
    padding-top: 200px;
    padding-bottom: 300px;
    background-color: #fef5e6;
    position: relative;

    &::after {
        content: '';
        position: absolute;
        bottom: -1px;
        left: 0;
        right: 0;
        height: 100px;
        clip-path: polygon(0 0, 100% 100%, 0 100%);
        background-color: #fff;
    }

    .fullscreen-offer {
        display: flex;
        align-items: center;

        .fullscreen-offer-text h1, p, button {
            margin-bottom: 20px;
        }

        .fullscreen-offer-text button {
            padding: 10px 20px;
            border-radius: 7px;
            background-color: #f35613;
            color: #fff;
            font-size: 16px;
            cursor: pointer;
            transition: .3s background-color;
    
            &:hover {
                background-color: #ff7d53;
            }
        }
    }

    .fullscreen-img {
        margin-left: 60px;
        position: relative;

        &::after {
            content: '';
            position: absolute;
            top: -60px;
            right: -30px;
            width: 200px;
            height: 200px;
            background-color: #f35613;
            border-radius: 50%;
            display: block;
            z-index: 0;
        }

        .big {
            width: 600px;
            border-radius: 15px;
            position: relative;
            z-index: 1;
        }

        .little {
            width: 500px;
            border-radius: 15px;
            position: absolute;
            bottom: -60px;
            right: -60px;
            z-index: 1;
        }
    }
}

.demonlist {
    display: flex;
    align-items: center;
    margin-top: 120px;

    .demonlist-img {
        margin-right: 60px;

        img {
            width: 600px;
            border-radius: 15px;
        }
    }

    .demonlist-text {
        h1, p {
            margin-bottom: 20px;
        }
    }
}

.hosting {
    display: flex;
    align-items: center;
    margin-top: 200px;

    .hosting-img {
        margin-left: 60px;
        position: relative;

        img {
            width: 600px;
            border-radius: 15px;
        }

        &::after {
            content: '';
            position: absolute;
            top: -60px;
            left: -40px;
            width: 200px;
            height: 200px;
            background-color: #f35613;
            border-radius: 50%;
            display: block;
            z-index: -1;
        }
    }

    .hosting-text {
        h1, p {
            margin-bottom: 20px;
        }
    }
}

.rate {
    display: flex;
    align-items: center;
    margin-top: 200px;
    margin-bottom: 200px;

    .rate-img {
        margin-right: 60px;
        position: relative;

        &::after {
            content: '';
            position: absolute;
            bottom: -60px;
            right: -40px;
            width: 200px;
            height: 200px;
            background-color: #f35613;
            border-radius: 50%;
            display: block;
            z-index: -1;
        }

        img {
            width: 600px;
            border-radius: 15px;
        }
    }

    .rate-text {
        h1, p {
            margin-bottom: 20px;
        }
    }
}

@media(max-width:1100px) {
    .fullscreen {
        .fullscreen-offer {
            display: flex;
            flex-direction: column;
            align-items: center;
    
            br {
                display: none;
            }

            .fullscreen-offer-text h1, p, button {
                margin-bottom: 20px;
            }
        }
    
        .fullscreen-img {
            margin-left: 0;
            margin-top: 100px;
            max-width: 900px;
    
            .big {
                width: 100%;
            }
    
            .little {
                width: 70%;
                right: -10px;
            }

            &::after {
                right: -20px;
            }
        }
    }

    .demonlist {
        flex-direction: column;
        justify-content: center;
        margin-top: 200px;
    
        .demonlist-img {
            margin: 0 auto;
            text-align: center;
            margin-bottom: 20px;
            max-width: 900px;
    
            img {
                width: 100%;
                margin-bottom: 60px;
            }
        }
    
        .demonlist-text {
            h1, p {
                text-align: center;
            }
        }
    }

    .hosting {
        flex-direction: column;
    
        .hosting-img {
            margin-left: 0;
            margin-top: 60px;
            position: relative;
            max-width: 900px;
    
            img {
                width: 100%;
            }
    
            &::after {
                left: -20px;
            }
        }
    
        .hosting-text {
            h1, p {
                text-align: center;
            }
        }
    }

    .rate {
        flex-direction: column;
    
        .rate-img {
            margin-right: 0;
            margin-bottom: 60px;
            position: relative;
            max-width: 900px;
    
            &::after {
                right: -20px;
            }
    
            img {
                width: 100%;
            }
        }
    
        .rate-text {
            h1, p {
                text-align: center;
            }
        }
    }
}