.dl {
    margin-top: 160px;

    .level {
        display: flex;
        align-items: center;
        padding: 20px;
        border-radius: 15px;
        // background-color: #e3e5fe;
        border: 1px solid #bababa;
        cursor: pointer;
        margin-bottom: 30px;
        transition: .3s transform;

        &:hover {
            transform: scale(102%);
        }

        .level-text {

        }

        img {
            height: 90px;
            width: 150px;
            border-radius: 7px;
            margin-right: 20px;
        }
    }
}